}

.search-bar {
  width: 100%;
  position: relative;
  display: flex; }
  .search-bar__input {
    width: 100%;
    border: 3px solid #443027;
    border-right: none;
    padding: 5px;
    height: 40px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #9d9998;
    background-color: #fffbfa;
    font-size: 22px; }
    .search-bar__input:focus {
      color: #443027; }
  .search-bar__submit-btn {
    width: 80px;
    height: 40px;
    border: 1px solid #443027;
    background: #443027;
    text-align: center;
    color: #E3E3E3;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 20px; }
    .search-bar__submit-btn:focus {
      outline: none;
      background: #747474; }

@media (min-width: 0px) and (max-width: 569px) {
  .search-bar__input {
    border: 3px solid #443027;
    border-radius: 5px; }
  .search-bar__submit-btn {
    display: none; } }

.dropdown {
  display: flex;
  align-items: center;
  position: relative;
  align-self: stretch;
  margin: 0 10px; }
  .dropdown__button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
    text-align: left;
    width: 200px;
    height: 40px;
    background-color: #443027;
    padding-left: 33px;
    border-radius: 5px;
    color: #E3E3E3;
    font-size: 20px;
    flex-grow: 1; }
    .dropdown__button:focus {
      background-color: #747474;
      outline: none; }
    .dropdown__button:before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border: 10px solid;
      border-color: #fff transparent transparent transparent;
      left: 8px;
      top: 33px; }
  .dropdown__list {
    list-style-type: none;
    margin: 0;
    padding: 0;
    top: 55px;
    right: 0;
    width: 200px;
    font-weight: bold;
    position: absolute;
    background-color: #E3E3E3;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    z-index: 1; }
  .dropdown__item {
    display: flex;
    border-bottom: 1px solid #E3E3E3; }
    .dropdown__item:hover {
      background-color: #e5e5e5;
      color: #E3E3E3; }
    .dropdown__item:last-child {
      border-bottom: none; }
    .dropdown__item button {
      padding: 0;
      border: none;
      font: inherit;
      color: inherit;
      background-color: transparent;
      cursor: pointer;
      display: inline-block;
      flex-grow: 1;
      text-align: left;
      padding: 5px 13px;
      border: solid 1px transparent;
      border-radius: 4px;
      text-decoration: none; }
      .dropdown__item button:hover {
        color: #ff3232;
        border-color: currentColor;
        background-color: #E3E3E3; }

@media (min-width: 720px) and (max-width: 1199px) {
  .dropdown {
    margin: 0 5px; }
    .dropdown__button {
      width: 100px; } }

@media (max-width: 719px) {
  .dropdown {
    margin: 0 0 0 2px; }
    .dropdown__button {
      width: 36px; } }

.header {
  position: fixed;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  background-color: #ec832f;
  height: 75px;
  padding: 0 50px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  font-family: Roboto, sans-serif; }
  .header__logo {
    max-height: 75px;
    background-color: #1a1a1a;
    color: #E3E3E3;
    text-align: center; }
  .header__search-container {
    display: flex;
    align-items: center;
    background-color: #ec832f; }
  .header__search-menu {
    width: 100px;
    background-color: #E3E3E3; }
  .header__display-mode {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 10px;
    width: 140px; }
  .header__sort-order-switch {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 4px;
    margin: 0 2px;
    color: #E3E3E3;
    font-size: 20px; }
    .header__sort-order-switch:focus {
      outline: none;
      background-color: grey; }
  .header__switch {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
    height: 40px;
    width: 40px;
    border-radius: 4px;
    margin: 0 2px; }
    .header__switch:focus {
      outline: none;
      background-color: grey; }
  .header .desc {
    background: url(data:image/png;base64,) no-repeat 0 50%;
    background-size: 32px 32px; }
  .header .asc {
    background: url(data:image/png;base64,) no-repeat 0 50%;
    background-size: 32px 32px; }
  .header .row {
    background: url(data:image/png;base64,) no-repeat 0 50%;
    background-size: 30px 30px; }
  .header .tile {
    background: url(data:image/png;base64,) no-repeat;
    background-size: 40px 40px; }

@media (min-width: 720px) and (max-width: 1199px) {
  .header {
    padding: 0 15px; }
    .header__display-mode {
      width: 100px;
      margin-left: 4px; } }

@media (max-width: 719px) {
  .header {
    padding: 0 5px; }
    .header__display-mode {
      flex-basis: 100px;
      margin-left: 4px; } }

@media (min-width: 0px) and (max-width: 569px) {
  .header__search-container {
    justify-content: start;
    margin: 0 auto; }
    .header__search-container form {
      flex-shrink: 3; }
    .header__search-container .dropdown {
      flex-shrink: 0; }
  .header__sort-order-switch {
    flex-shrink: 0;
    margin: auto; } }

@media (min-width: 0px) and (max-width: 569px) {
  .header__logo {
    display: none; } }

